*{
  z-index: 10;
}

.main{
  margin: 0;
  background-color: #0F172A;
  font-family: 'Courier New', monospace;
  color: whitesmoke;
  font-size: 1.2em;
  overflow-y: scroll;
  position: relative;
}

.cursor{
  position: fixed;
  width: 200vw;
  height: 200vw;
  background: radial-gradient(circle,rgb(28, 28, 58) 0%, rgb(15,23,35) 20%,  rgb(15, 23, 42) 100%);
  border-radius: 50%;
  pointer-events: none;
  transform: translate(-50%, -50%);
  overflow: hidden;
  z-index: -1;
}

.sidelinks{
  background-color: #f0f8ff;
  padding: 0.7rem 1rem;
  box-shadow: inset 4px 10px 10px 0px rgba(0, 0, 0, 1);}

.sidelinks *{
  margin: .5rem 0;
  color: #0F172A;
}

.experiencerow ul li ,.projectrow ul li{
  background-color: #d9c2ff5d;
  color: #d9c2ff;
  margin-right: .2rem ;
  padding: 0 .5rem;
}

.experiencerow .date{
  color: #d9c2ff;
}

.educationrow .date{
  color: #d9c2ff;
}